<template>
    <div>
        <layout-main>
             <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                <h1 class="h2">Detail Users</h1>
            </div>
            <div v-if="datausers == null" class="alert alert-danger" role="alert">
                {{errormessage}}
            </div>
            <div v-else class="card">
                <img src="../../../public/F.webp" class="card-img-top" alt="...">
                <div class="card-body">
                    <h5 class="card-title">{{datausers?.name}}</h5>
                    <address class="card-text">
                         <strong>{{datausers?.username}}</strong>
                        <br>
                        {{datausers?.address.street}}, {{datausers?.address.suite}} <br>
                        {{datausers?.address.city}}, {{datausers?.address.zipcode}} <br>
                        <abbr title="phone">{{datausers?.phone}}</abbr> <br>
                        {{datausers?.website}} <br>
                        {{datausers?.email}}<br>
                         <strong>{{datausers?.company.name}}</strong><br>
                         {{datausers?.company.catchPhrase}} 
                    </address>
                </div>
            </div>
        </layout-main>
    </div>
</template>

<script>
import LayoutMain from "@/views/LayoutMain"
export default 
{
  components: { LayoutMain },
  data()
  {
      return{
          id : this.$route.params.id,
          username : this.$route.params.username, 
          datausers: null,
          errormessage : null
      }
  },
  methods:
  {
    getDetailUser(id)
    {
        fetch("https://jsonplaceholder.typicode.com/users/" +id)
            .then(response => response.json()) //then 1, set response sebagai json
            .then(json => {
                if((json?.id!==undefined)){
                    this.datausers=json;
                }
                else
                {
                    this.errormessage = "Data User Tidak Ditemukan";
                }
                
            })//then 2, mengambil json dan ditampung/diset ke dalam data
            .catch(error =>
            {
                console.log(error);
                this.errormessage = "Maaf tidak dapat memuat data user, silahkan reload"
            })
    }
  },
  mounted ()
  {
      this.getDetailUser(this.id);
  }
}
</script>